import { Component, h, Host, Prop, Element, State } from '@stencil/core';
import ValidateInput from '../../input/validate';

@Component({
    tag: 'ks-page-login',
    styleUrl: 'login.css'
})
export class PageLogin {
    @Element() root: HTMLElement;
    
    @Prop() heading: string = "Logowanie do sklepu";
    @Prop() loggedIn: boolean = false;
    @Prop() ip: string;
    @Prop() host: string;

    @Prop() loginApi: string;
    
    @Prop() register: string = "Zarejestruj się";
    @Prop() registerLink: string;
    @Prop() accountLink: string;
    
    @Prop() recovery: string = "Odzyskiwanie hasła";
    @Prop() recovery_paragraph: string = "Jeżeli nie pamiętasz hasła, wpisz swój adres e-mail, a my prześlemy do Ciebie wiadomość z nowym hasłem.";
    @Prop() recovery_message: string = "Nowe hasło zostało wysłane na adres email podany w formularzu";
    @Prop() recoveryApi: string;
    @Prop() recoveryCheckApi: string;
    @Prop() recoveryCheckFailedMessage: string = "Konto z podanym adresem email nie istnieje u nas w bazie danych";

    @Prop() success_heading: string = "Zalogowano";
    @Prop() success_message: string = "Zostałeś pomyślnie zalogowany/a, za chwilę nastąpi przekierowanie na panel klienta.";

    @Prop() failure_heading: string = "Nie zalogowano";
    @Prop() failure_message: string = "Błędny adres email oraz hasło, spróbuj wprowadzić je ponownie lub skorzystaj z funkcji odzyskiwania hasła.";
    
    @State() loading: boolean = false;
    @State() notification_heading: string;
    @State() notification_message: string;

    componentDidLoad() {
        if(this.loggedIn) this.redirect_to_account();
    }

	render() {
		return <Host>
            <section class="container">
                <h1>{this.heading}</h1>

                <form id="login" enctype="application/x-www-form-urlencoded" onSubmit={(event)=>this.submit_login(event)}>
                    <ks-input-text email required name='email' label="Adres email" icon="mail" />
                    <ks-input-text password required name='haslo' label="Hasło" icon="key" />

                    <div class="buttons">
                        <ks-button rounded primary tall submit name="Zaloguj się" loading={this.loading} class="login" />
                        <ks-button rounded border link={this.registerLink} name={this.register} loading={this.loading} />
                        <ks-button rounded border onClick={() => this.show_recovery()} name={this.recovery} loading={this.loading} />
                    </div>
                </form>

                <ks-dialog id="recovery-dialog" dark>
                    <section>
                        <h1>{this.recovery}</h1>
                        <p>{this.recovery_paragraph}</p>
                        <form action={this.recoveryApi} method='post' id="recovery" enctype="application/x-www-form-urlencoded" onSubmit={(event)=>this.check_account_before_recovery(event)}>
                            <input type="hidden" name="adres_ip" value={this.ip} />
                            <input type="hidden" name="nazwa_hosta" value={this.host} />
                            <ks-input-text email required name='emailprzypomnienie' label="Adres email" icon="mail" />
                            <ks-button rounded submit name="Wyślij" loading={this.loading} />
                        </form>
                    </section>
                </ks-dialog>

                <ks-dialog id="notification-dialog" dark>
                    <section>
                        <h1>{this.notification_heading}</h1>
                        <p>{this.notification_message}</p>

                        <ks-button rounded onClick={()=>this.hide_notification()} name="Wróć do logowania" />
                    </section>
                </ks-dialog>

                <ks-overlay dark id="loading-overlay" close={false}>
                    <div class="success">
                        <h1>{this.success_heading}</h1>
                        <p>{this.success_message}</p>
                        <div class="loader">
                            <ks-loader large />
                        </div>
                    </div>
                </ks-overlay>
            </section>
        </Host>;
	}

    redirect_to_account() {
        const dialog = this.root.querySelector("#loading-overlay") as any;
        dialog.show();

        setTimeout(() => {
            window.location.href = this.accountLink;
        }, 1000);
    }

    show_recovery() {
        const dialog = this.root.querySelector("#recovery-dialog") as any;
        dialog.show();
    }

    hide_recovery() {
        const dialog = this.root.querySelector("#recovery-dialog") as any;
        dialog.hide();
    }

    show_notification(heading: string, message: string) {
        this.hide_recovery();
        this.notification_heading = heading;
        this.notification_message = message;

        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.show();
    }

    hide_notification() {
        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.hide();
    }

    async submit_login(event: Event) {
        event.preventDefault();
        this.loading = true;

        const form = this.root.querySelector("#login") as HTMLFormElement;

        if(!await ValidateInput(form)) {
            this.loading = false;
            return;
        }

        const filteredParams = new URLSearchParams();

        new FormData(form).forEach((value, key) => {
            filteredParams.append(key, value.toString());
        })

        const formData = new FormData();
        formData.append("data", filteredParams.toString());

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        await fetch(this.loginApi, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        })
        .then(response => {
            if(!response.ok) throw response.status;
            return response.text();
        })
        .then(response => {
            if(response.includes("Błąd"))
                this.show_notification(this.failure_heading, this.failure_message);
            else this.redirect_to_account();
        })
        .catch(error => {
            console.log(error);
            let message = error;

            if(!navigator.onLine)
                message = "Brak połączenia z internetem.";

            else if(Number.isInteger(error))
                message = `Błąd połączenia - ${error}`;

            this.show_notification(this.failure_heading, message);
        });

        this.loading = false;
    }

    async check_account_before_recovery(event: Event) {
        event.preventDefault();
        this.loading = true;

        const form = this.root.querySelector("#recovery") as HTMLFormElement;

        if(!await ValidateInput(form)) {
            this.loading = false;
            return;
        }

        const filteredParams = new URLSearchParams();

        new FormData(form).forEach((value, key) => {
            if(key == "emailprzypomnienie")
                filteredParams.append(key, value.toString());
        })

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        await fetch(`${this.recoveryCheckApi}&${filteredParams.toString()}`, {
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        })
        .then(response => {
            if(!response.ok) throw response.status;
            return response.text();
        })
        .then(response => {
            this.loading = false;

            if(response.includes("false"))
                this.show_notification(this.recovery, this.recoveryCheckFailedMessage);
            else this.submit_recovery(event);
        })
        .catch(error => {
            console.log(error);
            let message = error;

            if(!navigator.onLine)
                message = "Brak połączenia z internetem.";

            else if(Number.isInteger(error))
                message = `Błąd połączenia - ${error}`;

            this.show_notification(this.recovery, message);
            this.loading = false;
        });
    }

    async submit_recovery(event: Event) {
        event.preventDefault();
        this.loading = true;

        const form = this.root.querySelector("#recovery") as HTMLFormElement;

        if(!await ValidateInput(form)) {
            this.loading = false;
            return;
        }

        const filteredParams = new URLSearchParams();

        new FormData(form).forEach((value, key) => {
            filteredParams.append(key, value.toString());
        })

        const formData = new FormData();
        formData.append("data", filteredParams.toString());

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        await fetch(this.recoveryApi, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        })
        .then(response => {
            if(!response.ok) throw response.status;
            this.show_notification(this.recovery, this.recovery_message);
        })
        .catch(error => {
            console.log(error);
            let message = error;

            if(!navigator.onLine)
                message = "Brak połączenia z internetem.";

            else if(Number.isInteger(error))
                message = `Błąd połączenia - ${error}`;

            this.show_notification(this.recovery, message);
        });

        this.loading = false;
    }
}
