ks-page-login {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    
}

ks-page-login .container {
    max-width: 100%;
    width: 500px;
    padding: 30px;
    background-color: #fff;
    border-radius: 10px;
}

ks-page-login .buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 30px;
}

ks-page-login .container > h1 {
    text-align: center;
    margin: 10px 0px 5px 0px;
}


ks-page-login .success {
    text-align: center;
}

ks-page-login .success .loader {
    position: relative;
    width: 100px;
    height: 100px;
    margin: auto;
}

ks-page-login ks-dialog .content {
    width: 100%;
}

ks-page-login #notification-dialog section ks-button {
    margin-top: 20px;
}

ks-page-login .login > * {
    font-size: 18px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

@media (max-width: 1060px) {
    ks-page-login {
        background-color: #fff;
    }

    ks-page-login .container {
        padding: 40px 20px;
    }
}